.mine-index {
  padding-bottom: 0.6rem;
}
.mine-index .mine-con {
  width: 3.75rem;
  height: 2.1rem;
  background: url(../../static/media/bg.bb133b17.png) no-repeat left center;
  background-size: 100%;
  text-align: center;
}
.mine-index .mine-con > img {
  width: 1.5rem;
  padding-top: 0.6rem;
}
.mine-index .mine-skip {
  width: 3rem;
  border-radius: 0.08rem;
  box-shadow: 0rem 0.06rem 0.16rem 0rem rgba(0, 0, 0, 0.1);
  margin: 0 auto;
  position: relative;
  top: -0.7rem;
  margin-bottom: 0.3rem;
}
.mine-index .mine-skip .skip-i {
  width: 100%;
  height: 0.7rem;
  background-color: #ffffff;
  border-bottom: 0.01rem solid #EEEEEE;
  display: flex;
}
.mine-index .mine-skip .skip-i img {
  width: 0.46rem;
  vertical-align: middle;
  margin-right: 0.28rem;
  padding-left: 0.24rem;
  align-self: center;
  flex: 1 1;
}
.mine-index .mine-skip .skip-i span {
  color: #333333;
  font-size: 0.15rem;
  align-self: center;
  flex: 3 1;
}
.mine-index .mine-skip .skip-i .right {
  font-size: 0.2rem;
  color: #c7c7cc;
  margin-right: 0.27rem;
  flex: 1 1;
  text-align: right;
}
.mine-index .mine-skip .skip-i:first-child {
  border-radius: 0.08rem 0.08rem 0 0;
}
.mine-index .mine-skip .skip-i:last-child {
  border-radius: 0 0 0.08rem 0.08rem;
  border-bottom: none;
}
.mine-index .mine-skip .skip-i-one {
  border-radius: 0.08rem;
}
