.footer {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
  height: 0.45rem;
  background-color: #ffffff;
  box-shadow: 0 -0.01rem 0px 0px #bbbbbb;
  padding-top: 0.05rem;
}
.footer .footer-item {
  width: 25%;
  height: 100%;
  text-align: center;
  float: left;
  color: #aaaaaa;
  font-size: 0.1rem;
}
.footer .footer-item img {
  width: 0.2rem;
}
.footer .footer-item span {
  display: block;
  height: 0.14rem;
  font-size: 0.1rem;
}
.footer .footer-item span.active {
  color: #E9B551;
}
