.vip-control {
  width: 3rem;
  height: 1rem;
  border-radius: 0.08rem;
  background-color: #ffffff;
  box-shadow: 0rem 0.06rem 0.16rem 0rem rgba(0, 0, 0, 0.1);
  margin: 0 auto;
  position: relative;
  top: -0.7rem;
}
.vip-control img {
  width: 0.46rem;
  margin: 0.22rem;
  vertical-align: middle;
}
.vip-control .vip-control-r {
  float: right;
  width: 2rem;
  padding: 0.13rem 0;
}
.vip-control .vip-control-r div {
  line-height: 0.21rem;
  color: #333333;
  font-size: 0.15rem;
  margin-bottom: 0.08rem;
}
.vip-control .vip-control-r div span {
  color: #DBA131;
  padding: 0 0.2rem;
}
.vip-control .vip-control-r p {
  line-height: 0.17rem;
  color: #dba131;
  font-size: 0.13rem;
  width: 2rem;
  word-wrap: break-word;
  word-break: normal;
}
